:root{
  --size_menu: 20px;
}

.App {
  text-align: center;
}

.Colonne {
  display: flex;
  flex-direction: row;
}
/* Menu */
.Menu {
  background-color: #1f1f23;
  width: 15%;
  height: 100vh;
}

.App-header ul {
  list-style-type: none;
  padding-left: 10px;
  padding-right: 10px;
}

.App.App-header li {
  border-radius: 15px;
  
}

.App-header li a {
  display: block;
  width: 100%;
  margin: 0;
  margin-bottom: 10px;
  padding-bottom: 5px;
  padding-left: 5px;
  font-size: var(--size_menu);
  text-decoration: none;
 

  border-radius: 15px;
  color: rgb(255, 255, 255);
}
.App-header li:hover {
  background-color: rgb(255, 255, 255);
  border-radius: 15px 0px 0px 15px;
}

.App-header li:hover {
  background-color: rgb(255, 255, 255);
  border-radius: 15px;
}
.App-header li:hover a {
  color: #1f1f23;
}

/* Content */

.Content {
  background-color: rgb(242, 242, 242);
  width: 100%;
}
